:root {
  --padding-and-margin: 1.875rem; // 30px
}
.page-footer {
  background-color: $bg-footer-color;
  &__inner {
    padding: var(--padding-and-margin);
    padding-top: calc(var(--padding-and-margin) / 2 * 3); // 45px
    color: $text-footer-color;
  }
  &__list {
    @include listReset();
  }
  &__footer {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    padding-top: var(--padding-and-margin);
    border-top: 1px solid $text-footer-color;
    text-align: center;
    @include for-size(tablet-portrait-up) {
      flex-direction: row;
      text-align: left;
    }
  }
  & .logo {
    @include for-size(phone-only) {
      display: block;
      margin: auto;
      margin-bottom: var(--padding-and-margin);
    }
  }
  &__copyright {
    margin-left: 0.625rem;
    color: $text-footer-color;
    @include for-size(phone-only) {
      display: block;
      margin: auto;
      margin-bottom: var(--padding-and-margin);
    }
  }
  &__policy {
    margin-left: auto;
    @include linkReset();
    color: $text-footer-color;
    @include for-size(phone-only) {
      display: block;
      margin: auto;
    }
  }
  &__icon {
    width: 1.25rem;
    opacity: 0;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include for-size(phone-only) {
      margin-bottom: var(--padding-and-margin);
    }
  }
  &__list-item {
    width: 100%;
    flex-grow: 1;
    transition: 1s;
    @include for-size(tablet-portrait-up) {
      width: calc(100% / 2);
      padding: 0 calc(var(--padding-and-margin) / 2);
      margin-bottom: var(--padding-and-margin);
    }
    @include for-size(tablet-landscape-up) {
      width: calc(100% / 3);
      padding: 0 calc(var(--padding-and-margin) / 2);
      margin-bottom: var(--padding-and-margin);
    }
    @include for-size(desktop-up) {
      width: calc(100% / 5);
      padding: 0 calc(var(--padding-and-margin) / 2);
      margin-bottom: var(--padding-and-margin);
    }
    &.active {
      & .page-footer__sublist {
        display: block;
      }
    }
  }
  &__list-link {
    position: relative;
    display: block;
    margin-bottom: var(--padding-and-margin);
    color: $text-footer-color;
    @include linkReset();
    font-size: 130%;

    @include for-size(tablet-portrait-up) {
      pointer-events: none;
      font-size: 160%;
    }
  }
  &__icon {
    opacity: 1;
    position: absolute;
    top: 0;
    right: 0;

    @include for-size(tablet-portrait-up) {
      opacity: 0;
    }
  }
  &__sublist {
    display: none;
    @include listReset();
    font-family: $GoogleSans-Bold;

    opacity: 0;

    transform: translate3d(0, -2rem, 0);

    transition: all 0.5s;

    transition-timing-function: cubic-bezier(0.73, 0.56, 0.01, 1.01);

    @include for-size(phone-only) {
      margin-bottom: var(--padding-and-margin);
    }
  }
  &__sublist-item {
    position: relative;
    margin-bottom: calc(var(--padding-and-margin) / 3);
    &::before {
      content: "";
      position: absolute;
      top: 6px;
      left: 0;
      width: 10px;
      height: 10px;
      background-color: $marker-color;
      border-radius: 50%;
    }
  }
  &__sublist-link {
    display: block;
    padding-left: var(--padding-and-margin);
    color: $text-footer-color;
    @include linkReset();
    line-height: normal;


  }
}

.page-footer__list-item.active .page-footer__sublist {
  opacity: 1;

  transform: translateY(0);
}
