.mobmenu-inner {
  height: 0;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;

  &.active {
    padding-top: 1.5rem;
    height: auto;
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    // transition: opacity 1s;
  }
  &__list {
    @include listReset();
    position: relative;
    display: flex;
    margin: auto;
    flex-direction: column;
    width: 100%;
  }
  &__item {
    margin-bottom: 1.25rem;
  }
  &__link {
    position: relative;
    display: block;

    // opacity: 0;

    color: $text-color;
    font-weight: 600;
    text-decoration: none;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      width: 20px;
      height: 10px;
      background-image: url("../img/icons/icon-open_list.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      transition: 0.5s;
    }
    &:focus,
    &:hover {
      color: $color-primary;
    }
    &:active {
      &::before {
        top: 40%;
        transform: rotate(180deg);
        transition: 0.5s;
      }
    }
  }
  &__sub-list {
    display: none;
    @include listReset();
    padding: 1rem 0 0;
    &.active {
      display: block;
    }
  }
  &__sub-item {
    margin-bottom: 1.25rem;
  }
  &__sub-link {
    position: relative;
    display: block;
    color: $text-color;
    text-decoration: none;

    &::before {
      opacity: 0;

      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      width: 20px;
      height: 10px;
      background-image: url("../img/icons/icon-menu_arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    &:focus,
    &:hover {
      color: $color-primary;
      &:before {
        opacity: 1;
      }
    }
  }
}
