.sidemenu {
  display: flex;
  height: 100%;
  box-shadow: 5px 0 5px -5px #333;

  &__container {
    display: none;
    padding: 2.5rem;

    @include for-size(tablet-portrait-up) {
      display: block;
    }
  }
  &__logo {
    margin-bottom: 2.5rem;
  }
  &__navigation {
    margin-bottom: 2.5rem;
  }
  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2.5rem;
    padding-top: 1.25rem;
    border-top: 1px solid #eeeeee;
  }
  & .button {
    margin-top: 1.25rem;
  }
  &__btn-close {
    display: inline-block;
    vertical-align: middle;
    background-color: transparent;

    white-space: nowrap;
    user-select: none;
    cursor: pointer;
    border: none;
    border-radius: 0.3125rem;

    outline: none;

    transition: 1s;

    &:focus,
    &:hover {
      outline: 0;
    }
  }
  &__btn-icon {
    transform: rotate(45deg);
  }
  & .lang {
    margin-bottom: 1.25rem;
  }
  &__inner {
    opacity: 0;
    visibility: hidden;
    width: 0;
    transition: width 0.4s;
  }
  &__inner-container {
    padding-right: 2.5rem;
  }
  &__inner-menu {
    // display: none;

    width: 50%;
    height: 80%;
    position: absolute;
    top: 8rem;
    right: 0;
    opacity: 0;
    overflow-y: auto;
    pointer-events: none;

    transition: opacity 500ms, transform 800ms;
    &--active {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &__inner-list {
    @include listReset();
    position: relative;
    display: flex;
    margin: auto;
    flex-direction: column;
    width: 100%;
  }
  &__inner-item {
    margin-bottom: 1.25rem;
  }
  &__inner-link {
    position: relative;
    display: block;

    opacity: 0;

    // transform: translate3d(-3rem,0,0);

    // transition: all .3s;

    // transition-timing-function: cubic-bezier(.73,.56,.01,1.01);

    color: $text-color;
    font-weight: 600;
    text-decoration: none;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      width: 20px;
      height: 10px;
      background-image: url("../img/icons/icon-open_list.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      transition: 0.5s;
    }
    &:focus,
    &:hover {
      color: $color-primary;
    }
    &.active {
      &::before {
        top: 40%;
        transform: rotate(180deg);
        transition: 0.5s;
      }
    }
  }
  &__sub-list {
    display: none;
    @include listReset();
    padding: 1rem 0 0;
    &.active {
      display: block;
    }
  }
  &__sub-item {
    margin-bottom: 1.25rem;
  }
  &__sub-link {
    position: relative;
    display: block;
    color: $text-color;
    text-decoration: none;

    &::before {
      opacity: 0;

      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(-10%, -50%);
      width: 20px;
      height: 10px;
      background-image: url("../img/icons/icon-menu_arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    &:focus,
    &:hover {
      color: $color-primary;
      &:before {
        opacity: 1;
        transform: translate(10%, -50%);
        transition: all .5s;
      }
    }
  }
  &__btn-close {
    position: absolute;
    top: 0.625rem;
    right: 0.625rem;
  }
}

.sidemenu__inner.visible {
  width: 20rem;
  height: 100%;
  opacity: 1;
  visibility: visible;
  transition-delay: 0.1s;
  overflow-y: auto;

  background: #fff;

  transition: opacity 0.6s, width 0.4s;

  & .sidemenu__inner-link {
    opacity: 1;
  }
}
