.history {
  &__list {
    @include listReset();
    padding: calc(var(--padding-and-margin) * 2) 0;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    // padding: calc(var(--padding-and-margin)*2) 0;

    &:not(:last-child) {
      padding-bottom: calc(var(--padding-and-margin) * 2);

      & .history__icon {
        &::after {
          content: '';
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translate(-50%, 0%);
          width: 1px;
          height: calc(var(--padding-and-margin) * 10.5);
          border-left: 2px dashed darker($bg-color, 6%);

          @include for-size(tablet-portrait-up) {
            height: calc(var(--padding-and-margin) * 5);
          }
        }
      }
    }

    &:first-child {
      @include for-size(big-desktop-up) {
        padding-bottom: calc(var(--padding-and-margin) * 4);
      }
    }

    @include for-size(big-desktop-up) {
      padding-left: calc(var(--padding-and-margin) * 2);
      padding-right: calc(var(--padding-and-margin) * 2);
    }
  }
  &__icon {
    // flex-grow: 1;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3rem;
    height: 3rem;
    font-size: 120%;
    font-weight: 600;
    color: #ffffff;
    background-color: $color-warning;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 50%;

    @include for-size(tablet-portrait-up) {
      width: 5rem;
      height: 5rem;
    }
    @include for-size(tablet-landscape-up) {
      width: 6rem;
      height: 6rem;
    }
    @include for-size(desktop-up) {
      width: 8.125rem;
      height: 8.125rem;
    }

    &--primary {
      background-color: $color-primary;
    }
  }
  &__info {
    // flex-grow: 1;
    width: 65%;
    font-size: 80%;
    line-height: normal;
    color: $text-color;

    @include for-size(tablet-portrait-up) {
      font-size: 100%;
    }
    & p {
      // margin-bottom: calc(var(--padding-and-margin)*2);
    }
  }
}
