.page-contacts {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 2.5rem 0;
  overflow: hidden;
  &--bg {
    background-color: #eeeeee;
  }
  &--bg-map {
    @include for-size(desktop-up) {
      background-image: url('../img/russia_map.svg');
      background-repeat: no-repeat;
      background-position: right top;
      background-size: 55%;
    }
  }
  &--bg-pic {
    background-image: url('../img/bg/bg-1.png');
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: 70%;
  }
  & canvas {
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    // background-color: #dddddd;
    z-index: 2;
  }
  &__inner {
    position: relative;
    z-index: 3;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
  }
  &__item {
    width: 100%;
    @include for-size(desktop-up) {
      width: 45%;
    }
  }
  &__list {
    @include listReset();
    padding-top: calc(var(--padding-and-margin) * 2);

    &--fl {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
  &__list-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: var(--padding-and-margin);
  }
  &__list-col-50 {
    width: 100%;

    display: flex;
    flex-wrap: nowrap;
    align-items: center;

    margin-bottom: var(--padding-and-margin);

    @include for-size(tablet-portrait-up) {
      width: 45%;
    }
  }
  &__icon {
    width: 13%;
    margin-right: calc(var(--padding-and-margin) / 2);

    @include for-size(tablet-portrait-up) {
      margin-right: var(--padding-and-margin);
    }
  }
  &__text {
    width: 80%;
  }
  &__link {
    display: block;
    color: $text-color;
    font-weight: 600;
    text-decoration: none;
  }
  &__box {
    margin-bottom: 2.5rem;
  }
  &__pic {
    width: 30%;
    padding-right: 10px;
  }
  &__info {
    width: 70%;
    font-size: 80%;
    line-height: normal;
  }
}
