.navigation {
  &__list {
    @include listReset();
  }
  &__item {
    margin-bottom: 1.25rem;

    &.active {
      & .navigation__link {
        color: $color-primary;
        &:before {
          opacity: 1;
        }
      }
    }
  }
  &__link {
    position: relative;
    display: block;
    color: $text-color;
    text-decoration: none;

    &::before {
      opacity: 0;

      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(-10%, -50%);
      width: 20px;
      height: 10px;
      background-image: url("../img/icons/icon-menu_arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    &:focus,
    &:hover {
      color: $color-primary;
      &:before {
        opacity: 1;
        transform: translate(10%, -50%);
        transition: all .5s;
      }
    }
  }
  &__icon {
    width: 1.25rem;
    padding-right: 0.625rem;
    box-sizing: content-box;
  }
}
