.list {
  @include listReset();
  margin-bottom: 1.5rem;
  & li {
    position: relative;
    padding-left: 1.25rem;
    &::before {
      position: absolute;
      content: "";
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 10px;
      height: 10px;
      background-color: $color-primary;
      border-radius: 50%;
    }
  }
}
