.service {
  &__slide {
    width: 100%;
    margin-top: 1.5rem;

    @include for-size(tablet-landscape-up) {
      padding-left: 55%;
    }

    & .title {
      color: $color-primary;
      text-transform: inherit;
    }
  }

  & .parallax-bg {
    width: 100%;
    position: relative;

    padding: 20px 0;
    min-height: 12.5rem;

    @include for-size(tablet-landscape-up) {
      position: absolute;
      top: 1.5rem;
      width: 50%;
      height: 100%;
      max-height: 25rem;
    }
  }
  & .swiper-wrapper {
    z-index: 2;
    margin-top: $padding-and-margin;
  }
  & .swiper-slide {
    opacity: 0 !important;
  }
  & .swiper-slide-active {
    opacity: 1 !important;
  }
  & .swiper-pagination-bullet-active {
    background-color: $color-warning;
  }

  & .swiper-container {
    height: 100%;
    @include for-size(phone-only) {
      margin-top: -20px;
    }
  }

  & .swiper-container-horizontal > .swiper-pagination-bullets {
    opacity: 0;
    pointer-events: none;

    @include for-size(phone-only) {
      opacity: 1;
      pointer-events: auto;
      bottom: auto;
      left: auto;
      top: 0;
      right: -100px;
    }
  }
}
