.link {
  display: inline-block;
  vertical-align: middle;
  background-color: transparent;
  color: $text-color;
  border-radius: 0;
  text-decoration: none;
  transition: .4s;
  
  &:focus,&:hover {
    text-decoration: none;
    color: $color-primary;
  }

  &--uppercase {
    text-transform: uppercase;
  }

  &--btn {
    padding: $btn-padding;
    background-color: $btn-bg-color;
    color: $btn-tex-color;

    border-radius: 0.3125rem;

    &:focus,
    &:hover {
      text-decoration: none;
      background-color: darker($btn-bg-color, 2.5%);
    }
  }
  &--primary {
    font-weight: 600;
    text-transform: uppercase;
    background-color: $color-primary;

    &:focus,
    &:hover {
      text-decoration: none;
      background-color: darker($color-primary, 2.5%);
    }
  }
}
