.page {
  position: relative;
  background-color: $bg-color;
  @include for-size(tablet-landscape-up) {
    display: flex;
    justify-content: space-between;
  }
  & .wrapper {
    width: 100%;
    margin-left: auto;
    @include for-size(tablet-portrait-up) {
      width: calc(100%);
    }
    @include for-size(tablet-landscape-up) {
      width: calc(100% - 31vw);
    }
    @media (min-width: 950px) {
      width: calc(100% - 28vw);
    }
    @media (min-width: 1000px) {
      width: calc(100% - 27vw);
    }
    @media (min-width: 1050px) {
      width: calc(100% - 26vw);
    }
    @include for-size(desktop-up) {
      width: calc(100% - 22vw);
    }
    @media (min-width: 1300px) {
      width: calc(100% - 20vw);
    }
    @include for-size(big-desktop-up) {
      width: calc(100% - 20vw);
    }
  }
  &__content {
    padding-top: 2.5rem;
  }

  &--canvas {
    position: relative;
    overflow-x: hidden;

    & canvas {
      position: absolute;
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 0;
      z-index: -1;
    }

  }
}
