.tabs {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  height: 100%;

  overflow: hidden;

  &__box {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__list {
    @include listReset();

    display: flex;
    align-items: center;

    width: 100%;

    overflow-x: auto;
  }

  &__item {
    @include rem(padding-right, 10px);

    white-space: nowrap;

    &:last-child {
      padding-right: 0;
    }
  }
  &__link {
    display: block;
    @include rem(padding, 10px 0);
    text-decoration: none;
    // color: $color-turquoise;

    // font-family: $font-base-medium;
    font-weight: 500;
    line-height: normal;
    transition: 0.5s;

    position: relative;

    // &::before {
    //   position: absolute;
    //   content: "";
    //   bottom: 0;
    //   left: 0;
    //   width: 100%;
    //   height: 2px;
    //   background: transparent;

    //   transition: 0.5s;
    // }
  }
  &__content {
    position: relative;
    z-index: 2;
    height: 100%;
    width: 100%;

    &.scroll-content {
      height: 100%;
      overflow-y: auto;
    }
  }
  &__content-inner {
    height: 100%;
    overflow: auto;
  }

  &__btn-box {
    display: flex;
  }
  &__btn {
    margin: 0 5px;
  }
}

.tabs__item.active .tabs__link,
.tabs__item .tabs__link:hover {
  opacity: 1;
  color: $color-primary;

  // &::before {
  //   background: #333333;
  // }
}

.tabs__panel {
  display: none;
}

.tabs__panel.active {
  display: block;
  height: 100%;
}

.tabs__panel p + div {
  @include rem(margin-top, 15px);
}

@media screen and (max-width: 600px) {
  .tabs {
    flex-direction: column;
  }

  .tabs__item {
    width: 100%;
  }

  .tabs__item:not(:last-child) {
    margin-right: 0;
  }

  .tabs__item .tabs__link {
    border-radius: 0;
    opacity: 1;
    top: 0;
  }

  .tabs__content {
    border-radius: 0;
  }
}
