

// Глобальные переменные

$color-primary: #1b608e;
$color-secondary: #333333;
$color-warning: #f08518;


$text-color: #333333;
$link-color: #1b608e;
$bg-color: #f7f7f8;
$marker-color: $color-warning;
$btn-bg-color: $color-warning;
$btn-tex-color: #ffffff;
$btn-padding: .625rem 1.25rem;


$bg-footer-color: #333333;
$text-footer-color: #ffffff;

$bg-form-color: $color-primary;
$text-form-color: #ffffff;


$padding-and-margin: 1.25rem;




// шрифты
$GoogleSans: "GoogleSans-Regular";
$GoogleSans-Medium: "GoogleSans-Medium";
$GoogleSans-Bold: "GoogleSans-Bold";
$font-base: $GoogleSans, "Arial", sans-serif;



:root {
  --padding-and-margin: 1.25rem;
}
