.title {
  margin-bottom: 1.25rem;
  margin-right: 3.125rem;
  font-family: $GoogleSans-Bold;
  font-size: 100%;
  line-height: normal;
  text-transform: uppercase;
  color: $text-color;

  @include for-size(tablet-landscape-up) {
    margin-right: 0;
    font-size: 156.5%;
  }
  &__count {
    font-size: 80%;
    color: $color-warning;
  }
}
