.button {
  display: inline-block;
  vertical-align: middle;
  padding: $btn-padding;
  background-color: $btn-bg-color;
  color: $btn-tex-color;

  white-space: nowrap;
  user-select: none;
  cursor: pointer;
  border: none;
  border-radius: 0.3125rem;

  outline: none;

  transition: 1s;

  &:focus,
  &:hover {
    outline: 0;

    background-color: darker($btn-bg-color, 2.5%);
  }

  &--uppercase {
    text-transform: uppercase;
  }

  &--link {
    padding: 0;
    background-color: transparent;
    color: $text-color;
    border-radius: 0;
    &:focus,
    &:hover {
      background-color: transparent;
    }
  }
  &--primary {
    font-weight: 600;
    text-transform: uppercase;
    background-color: $color-primary;

    &:focus,
    &:hover {
      outline: 0;

      background-color: darker($color-primary, 2.5%);
    }
  }
}
