.advantages {
  &__list {
    display: flex;
    flex-wrap: wrap;
    @include listReset();
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    width: 50%;
    margin-bottom: var(--padding-and-margin);

    &:nth-child(2),
    &:nth-child(4) {
      & .advantages__icon {
        background-image: url('../img/icons/icon-badge_2.svg');
      }
    }

    @include for-size(tablet-portrait-up) {
      width: 30%;
    }
    // @include for-size(tablet-landscape-up) {
    //   width: 20%;
    // }
    @include for-size(desktop-up) {
      width: 20%;
    }
  }
  &__icon {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: var(--padding-and-margin);

    width: 8rem;
    height: 8rem;

    background-image: url('../img/icons/icon-badge_1.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    color: #ffffff;

    @include for-size(tablet-portrait-up) {
      width: 10rem;
      height: 10rem;
    }
    @include for-size(desktop-up) {
      width: 12.5rem;
      height: 12.5rem;
    }

    &--pic {
      background-image: url('../img/icons/icon-badge_gerb.svg');
    }
  }
  &__icon-text {
    display: block;
    
    font-size: 120%;
    font-weight: 600;
    text-align: center;

    &--number {
      display: block;
      font-size: 300%;
      font-weight: 600;
      margin-bottom: var(--padding-and-margin);
    }
  }

  &__text {
    margin-bottom: auto;
    font-size: 100%;
    line-height: normal;
    font-weight: 600;
    text-align: center;
  }
}
