:root {
  --padding: 0.625rem 1.25rem;
  --mb: 1.25rem;
  --border-radius: 5px;
}
.page-form {
  padding: 2.5rem;

  background-color: $bg-form-color;

  @include for-size(phone-only) {
    padding: 0.9375rem;
  }
  &__inner {
    border: none;
  }
  &__title {
    margin-bottom: var(--mb);
    color: $text-form-color;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    @include listReset();
  }
  &__item {
    width: 100%;

    @include for-size(tablet-portrait-up) {
      width: 48%;
      &--full {
        width: 100%;
      }
    }
  }
  &__field {
    display: block;
    padding: var(--padding);
    margin-bottom: var(--mb);
    width: 100%;
    border: none;
    border-radius: var(--border-radius);
  }
  &__comment {
    display: block;
    padding: var(--padding);
    margin-bottom: var(--mb);
    width: 100%;
    height: 6rem;
    resize: none;
    border: none;
    border-radius: var(--border-radius);
  }
  & .button {
    display: block;
    margin-left: auto;
  }
}
