.mob-navigation {
  padding: 0 0.9375rem;
  @include for-size(tablet-landscape-up) {
    display: none;
  }
  &__closed {
    & svg {
      & rect {
        &:first-child {
          transform: rotate(45deg) translateX(1.5px);
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:nth-child(3) {
          transform: rotate(-45deg) translateX(-5.5px);
        }
      }
    }
  }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.9375rem 0;
  }
  &__footer {
    display: flex;
    flex-direction: column;

    padding-top: 1.5rem;
    border-top: 1px solid #d8d8d8;
  }
  &__inner {
    height: 0;
    transform: translateX(-200%);

    &.active {
      z-index: 5;
      position: absolute;
      top: 0;
      left: 0;
      padding: 0.9375rem;

      width: 100%;
      height: 100%;

      background: #ffffff;
      transform: translateX(0);
      transition: transform 1s;

      overflow-y: auto;
    }
    &.closed {
      z-index: 5;
      position: absolute;
      top: 0;
      left: 0;
      padding: 0.9375rem;

      width: 100%;
      height: 100%;

      background: #ffffff;

      transform: translateX(-200%);
      transition: transform 1s;
    }
  }
  &__closed {
    margin-bottom: 1rem;
  }
  &__contacts {
    display: flex;
    flex-direction: column;
    margin-bottom: 1.5rem;
  }
  &__lang {
    margin-bottom: 1.5rem;
  }
  &__social {
    margin-bottom: 1.5rem;
  }
  &__list {
    @include listReset();
  }
  &__item {
    margin-bottom: 1.25rem;

    &.active {
      & .navigation__link {
        color: $color-primary;
        &:before {
          opacity: 1;
        }
      }
    }
  }
  &__link {
    position: relative;
    display: block;
    color: $text-color;
    text-decoration: none;

    &::before {
      opacity: 0;

      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      width: 20px;
      height: 10px;
      background-image: url("../img/icons/icon-menu_arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    &:focus,
    &:hover {
      color: $color-primary;
      &:before {
        opacity: 1;
      }
    }
  }
  &__icon {
    width: 1.25rem;
    padding-right: 0.625rem;
    box-sizing: content-box;
  }
}
