
.block-preview {
  display: block;
  height: 100%;
  text-decoration: none;
  color: $text-color;

  &--form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    min-height: calc(40vh + 5.5rem + var(--padding-and-margin));

    background-color: $color-primary;

    & .block-preview__info {
      padding: var(--padding-and-margin);
      text-align: center;
      color: #ffffff;
    }
    & .block-preview__title {
      color: #ffffff;
    }
    & .block-preview__title {
      font-weight: 600;
      font-size: 160%;
    }
  }

  &:focus,
  &:hover {
    & .block-preview__pic {
      &::before,
      &::after {
        opacity: 1;
        transition: 1s;
      }
    }
  }
  &__pic {
    position: relative;
    margin-bottom: var(--padding-and-margin);
    width: 100%;
    // height: 100%;
    max-height: 30rem;
    object-fit: cover;

    &::before {
      opacity: 0;
      z-index: 2;
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      @include background-opacity($color-primary, 0.6);
    }
    &::after {
      opacity: 0;
      z-index: 2;
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 2.5rem;
      height: 2.5rem;
      background-image: url("../img/icons/icon-details.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }

    &--small {
      height: 30vh;
    }
    &--middle {
      height: 35vh;
    }
    &--large {
      height: 40vh;
    }
  }
  &__info {
    @include for-size(phone-only) {
      --padding-and-margin: 1.25rem;
      margin-top: var(--padding-and-margin);
    }
  }
  &__title {
    margin-bottom: var(--padding-and-margin);
    color: $color-primary;
    font-size: 110%;
    font-weight: 300;
    line-height: normal;
  }
  &__date {
    margin-bottom: var(--padding-and-margin);
    opacity: 0.7;
  }
  &__text {
    opacity: 0.7;
  }
  &__marker {
    display: inline-block;
    margin: 0.3125rem;
    padding: 0.1875rem 0.3125rem;
    font-size: 80%;
    color: $text-color;
    opacity: 0.7;
    text-transform: lowercase;
    border-radius: 5px;
    border: 1px solid $text-color;
    word-break: break-all;
  }
}
